.side-nav {
  display: none;
}

@media screen and (min-width: $break-narrow) {
  .side-nav {
    display: block;
    position: relative;
    min-height: 100vh;
    background-color: #000;
    width: 185px;

    .side-container {
      position: fixed;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 185px;
      .nav-icon {
        height: 60px;
        max-width: 190px;
        width: 100%;
        flex: 1;
        margin-block-end: 18px;
        padding: 0 24px;
      }
      ul {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 16px;
        margin: 0;
        padding: 0;
        width: 100%;
        padding-bottom: 30px;
        &:last-child {
          padding-bottom: 0;
        }

        & > * {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          gap: 14px;
          width: 100%;
          height: 40px;
          padding: 0 8px;
          color: #fff;
          border-radius: 4px;
          cursor: pointer;
          .txt {
            overflow: hidden;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            display: -webkit-box;
            word-break: break-all;
            text-overflow: ellipsis;
            white-space: nowrap;
            color: #b3b3b3;
            font-size: 14px;
            font-weight: 700;
            letter-spacing: normal;
            line-height: 16px;
            text-transform: none;
            max-width: 110px;
          }
          img {
            width: 24px;
            height: 24px;
          }
          .lib {
            margin-left: 5px;
            & > * {
              font-size: 14px;
              color: #fff;
            }
          }
          .add-container {
            position: relative;
            background-color: #b2b2b2;
            border-radius: 1px;
            height: 24px;
            width: 24px;
            .add {
              color: #808080;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
            }
            &:hover {
              background-color: #b2b2b2;
            }
          }
          .liked {
            background: linear-gradient(135deg, #450af5, #c4efd9);
            display: flex;
            align-items: center;
            justify-content: center;
            align-content: center;
            .favorite {
              color: #fff;
              width: 12px;
              height: 12px;
            }
          }
          &:hover {
            .txt {
              color: #fff;
            }
          }
        }
        .active-txt {
          .txt {
            color: #fff;
          }
        }
      }
      .form-control-xsm {
        cursor: pointer;
        margin-block-end: 25px;
        border-radius: 4px;
        background-color: #c9b3b3a1;
      }
    }
  }
}
